import { sanityClient } from "@/lib/sanity";
import BlogClient from "./BlogClient";

export async function generateMetadata({ params }) {
  const slug = Array.isArray(params.slug)
    ? params.slug[0]
    : params.slug;

  if (!slug) {
    return {
      title: "Blog | Cloud101",
      description: "Salesforce and Cloud learning articles by Cloud101."
    };
  }

  const post = await sanityClient.fetch(
    `*[_type=="post" && slug.current==$slug][0]{
      title,
      excerpt
    }`,
    { slug }
  );

  if (!post) {
    return {
      title: "Blog | Cloud101",
      description: "Salesforce and Cloud learning articles by Cloud101."
    };
  }

  const title = `${post.title} | Cloud101`;
  const description =
    post.excerpt?.slice(0, 155) ||
    "Learn Salesforce and cloud technologies with real-world examples at Cloud101.";

  return {
    title,
    description,
    alternates: {
      canonical: `https://cloud101.in/blog/${slug}`
    },
    openGraph: {
      title,
      description,
      type: "article",
      url: `https://cloud101.in/blog/${slug}`
    },
    twitter: {
      card: "summary_large_image",
      title,
      description
    }
  };
}

export default function Page({ params }) {
  const slug = Array.isArray(params.slug)
    ? params.slug[0]
    : params.slug;

  return <BlogClient slug={slug} />;
}
